import { element } from 'protractor';
import { Router, ActivatedRoute } from '@angular/router';
import { MarkerService } from './marker.service';
import { Component, OnInit, HostBinding } from '@angular/core';
import { Subscription } from 'rxjs/Rx';
import { fallIn } from '../../router.animations';

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css'],
  providers: [MarkerService],
  animations: [fallIn()]
})
export class MapComponent implements OnInit {
  @HostBinding('@fallIn') routeAnimation = true;

  private routeFragmentSubscription: Subscription;
  title: string = 'Co kde najdete';
  lat: number = 50.8058000;
  lng: number = 14.0178372;
  zoom: number = 11;
  markers: Marker[] = [];
  constructor(markerService: MarkerService, private router: Router, private route: ActivatedRoute) {
    this.markers = markerService.getMarkers();
   }

  ngOnInit() {
    window.scrollTo(0, 300);
  }

  markerClicked(marker: Marker) {
    console.log(marker);
  }

  showOnMap(lat: number, lng: number) {
    this.lat = lat;
    this.lng = lng;
    this.zoom = 15;
    // scroll to the map element
    const element = document.getElementById('weddingMap');
    if (element) {
      element.scrollIntoView();
    }
  }

}



interface Marker {
  lat: number;
  lng: number;
  name: string;
}



