import { Component, OnInit, HostBinding } from '@angular/core';
import { AngularFire, AuthProviders, AuthMethods } from 'angularfire2';
import { Router } from '@angular/router';
import { moveIn, fallIn, moveInLeft } from '../router.animations';

@Component({
  selector: 'app-wedding',
  templateUrl: './wedding.component.html',
  styleUrls: ['./wedding.component.css'],
  animations: [fallIn()]
})
export class WeddingComponent implements OnInit {
  name: string;
  avatar: string = 'https://encrypted-tbn2.gstatic.com/images?q=tbn:ANd9GcQ2f0v_Sg9WGHPAzHVUCfniqUnmteo3B_a7KzH_aslfXdV9kk2plg';
  state: string = '';

  @HostBinding('@fallIn') routeAnimation = true;
  constructor(public af: AngularFire, private router: Router) {

    this.af.auth.subscribe(auth => {
      if (auth) {
        console.log(auth);
        this.name = auth.auth.displayName;
        this.avatar = auth.auth.photoURL;
      }
    });

  }

  logout() {
     this.af.auth.logout().then( () => {
       this.router.navigateByUrl('/login');
     });
  }


  ngOnInit() {
  }
}



