import { ActivatedRoute } from '@angular/router';
import { AngularFire, FirebaseListObservable } from 'angularfire2';
import { Component, OnInit, Input, OnChanges, OnDestroy, trigger, transition, style, animate, HostBinding } from '@angular/core';
import {
  FormGroup,
  FormControl,
  Validators,
  FormBuilder,
  FormArray
} from '@angular/forms';
import { Observable, Subscription } from 'rxjs/Rx';
import { fallIn } from '../../router.animations';


@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  animations: [
    trigger(
      'enterAnimation', [
        transition(':enter', [
          style({transform: 'translateX(100%)', opacity: 0}),
          animate('500ms', style({transform: 'translateX(0)', opacity: 1}))
        ]),
        transition(':leave', [
          style({transform: 'translateX(0)', opacity: 1}),
          animate('500ms', style({transform: 'translateX(100%)', opacity: 0}))
        ])
      ]
    ),
    fallIn()
  ],
  styleUrls: ['./form.component.css']
})
export class FormComponent implements OnInit, OnChanges, OnDestroy {
  @HostBinding('@fallIn') routeAnimation = true;

  private weddingForms: FirebaseListObservable<any>;
  private myForm: FormGroup;
  private overnight = false;
  private formSent = false;
  private subscription: Subscription;
  private password: String;
  name: string;
  avatar: string;

  private nights = {
    'čtvrtek': '7.9. 2017',
    'pátek': '8.9. 2017',
    'sobota': '9.9. 2017',
  };

  beddings = [
    'Vlastní spacák',
    'Místní ložní prádlo'
  ];

  keys(): Array<string> {
    return Object.keys(this.nights);
  };

  constructor(private formBuilder: FormBuilder, private _af: AngularFire, private route: ActivatedRoute) {
    this.subscription = this.route.queryParams.subscribe(
      queryParam => {
        this.name = queryParam['name'] !== 'undefined' ?  queryParam['name'] : '';
        this.avatar = queryParam['avatar'];
      }
    )
    this.weddingForms = _af.database.list('/weddingForms');
    this.myForm = formBuilder.group({
      'guest': formBuilder.group({
        'comes': [true],
        'name': [ this.name, Validators.required],
        'guestCount': ['2', [ Validators.required, Validators.pattern('[0-9]')]],
        'avatar': [this.avatar]
      }),
      'acommodation': formBuilder.group({
        'nights': formBuilder.group({
          'čtvrtek': [false],
          'pátek': [false],
          'sobota': [false]
        }),
        'bedding': ['Vlastní spacák', Validators.required]
      }),
      drive: [false],
      'food': formBuilder.group({
        'food': ['Bez omezení'],
        'drink': ['']
      }),
      'music': ['']
    });
   }

  ngOnInit() {
    window.scrollTo(0, 300);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  ngOnChanges(changes) {
    if (changes.name) {
      (<FormGroup>this.myForm.controls['guest']).controls['name'].setValue(changes.name.currentValue);
    }
    if (changes.avatar) {
      (<FormGroup>this.myForm.controls['guest']).controls['avatar'].setValue(changes.avatar.currentValue);
    }
  }

  onSubmit() {
    console.log('Form data: ', this.myForm.value);
    this.weddingForms.push(this.myForm.value).then(() => {
      this.formSent = true;
      alert('Děkujeme za vyplnění formuláře');
    });
  }
}



